<!-- eslint-disable -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      {{ item.product.id }}
    </span>
    <div class="product__counter form__counter">
      <button type="button" @click="amount > 1 ? amount -= 1 : amount = 1" aria-label="Убрать один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>
      <input type="text" v-model.number="amount" name="count">
      <button type="button" @click="amount += 1" aria-label="Добавить один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>          
    </div> 
    <div class="product__adding-status" v-if="productAddedStatus">Количество товара в корзине изменилось</div>  
    <b class="product__price">
      {{ $filters.numberFormat(item.amount * item.product.price) }} &#8381;
    </b>
    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct()">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
// import { mapMutations } from 'vuex';

export default {
  props: ['item'],
  data() {
    return {
      productAddedStatus: false,
    };
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value }).then(() => {
          this.productAddedStatus = true;
          setTimeout(() => {
            this.productAddedStatus = false;
          }, 1000);
        });
      },
    },
  },
  methods: {
    // ...mapMutations({ deleteProduct: 'deleteCartProduct' }),
    deleteProduct() {
      this.$store.dispatch('deleteProductFromCart', { productId: this.item.productId });
    },
  },
};
</script>
