<!-- eslint-disable max-len -->
<template>
  <ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" href="#" aria-label="Предыдущая страница"
            :class="{'pagination__link--disabled' :currentPage === 1}"
            @click.prevent="paginatePrev()">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <a href="#" class="pagination__link"
            :class="{'pagination__link--current' :pageNumber === currentPage}"
            @click.prevent="paginate(pageNumber)">
              {{ pageNumber }}
            </a>
          </li>
          <li class="pagination__item">
           <a class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница"
            :class="{'pagination__link--disabled' :currentPage === pages}"
            @click.prevent="paginateNext()">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
        </ul>
</template>

<script>

export default {
  props: ['currentPage', 'count', 'perPage'],
  emits: ['update:currentPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('update:currentPage', page);
    },
    paginateNext() {
      this.$emit('update:currentPage', this.currentPage < this.pages ? this.currentPage + 1 : this.currentPage);
    },
    paginatePrev() {
      this.$emit('update:currentPage', this.currentPage > 1 ? this.currentPage - 1 : this.currentPage);
    },
  },
};

</script>
